import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import axios from "axios";

function App() {

  const [origin, setOrigin] = useState(false);
  const [show, setShow]     = useState(false);
  const username = window.location.hostname.split(".")[0];

  const uri = window.aff ? "/landing/" + window.aff : "";

  (async ()=>{
    const {data} = await axios.request({
      method:"GET",
      url:"https://9.9.9.9:5053/dns-query?name="+ username +".cname.glados.network&type=TXT"
    });
    setOrigin("https://"+JSON.parse(data.Answer[0].data) + uri);
  })()

  return (
    <React.Fragment>
      {origin?
        <iframe src={origin} height="100%" width="100%" frameBorder="0"></iframe>
        :
        null
      }
    </React.Fragment>
  );
}

export default App;
